<template>
	<view class="win-again" :style="{display: isShow?'block':'none'}">
		<!-- 旋转背景  -->
		<image class="animated bounceInBgRotate01  bg-rotate" src="../../../static/animWinAgain/img_7.png"></image>
		<image class="animated bounceInBgRotate02  bg-rotate" src="../../../static/animWinAgain/img_7.png"></image>
		<!-- 背景一 -->
		<image class="animated bounceInBg01 bg-01" src="../../../static/animWinAgain/img_5.png"></image>
		<!-- 背景二 -->
		<image class="animated bounceInBg02 bg-02" src="../../../static/animWinAgain/img_6.png"></image>
		<!-- 礼盒 -->
		<image class="animated tadaGiftBoxTop gift-box-top delay-1s" src="../../../static/animWinAgain/img_1.png" />
		<image class="animated tadaGiftBoxBottom gift-box-bottom delay-1s"
			src="../../../static/animWinAgain/img_2.png" />
		<!-- 恭喜您又中奖了 -->
		<image class="gift-box-again animated bounceInAgainText delay-1s"
			src="../../../static/animWinAgain/img_4.png" />
		<!-- 卡劵 -->
		<view class="animated bounceInCardCoupons card-coupons">
			<!-- 25or26周年 -->
			<image v-if="prizeratetype !== 2" class="cc-left-icon" src="../../../static/images/mcb_no_converted25.png">
			</image>
			<image v-else class="cc-left-icon" src="../../../static/images/mcb_no_converted26.png"></image>
			<!-- 右边背景 -->
			<image class="cc-right-bg" src="../../../static/images/mcb_bg_white.png"></image>
			<view class="cc-right-info">
				<view class="cc-r-i-title">{{prizeratetype !== 2?'1元轻松享兑换劵':'1元享红牛兑换券'}}</view>
				<view class="cc-r-i-time">领取时间：{{time}}</view>
			</view>
		</view>
		<!-- 按钮部分 -->
		<view class="animated lightSpeedIn xwwh-xh-dialog-btn">
			<view class="xdb-item" @click="goCardBag">
				<view class="xdb-item-text deposit">存入卡包</view>
				<image class="xdb-item-bg" src="../../../static/images/dialog_btn_bg02.png"></image>
			</view>
			<view class="xdb-item">
				<button v-if="userInfo.mobile" class="xdb-item exchange" @click="exchange">马上兑换</button>
				<button v-else class="xdb-item exchange" open-type="getPhoneNumber"
					@getphonenumber="exchangeBefore">马上兑换</button>
				<image class="xdb-item-bg" src="../../../static/images/dialog_btn_bg01.png"></image>
			</view>
		</view>
		<!-- 黑幕 -->
		<view class="xh-dialog-black" @touchmove.stop></view>
	</view>
</template>

<script>
	import {
		mapGetters,
		mapMutations,
		mapActions
	} from 'vuex'
	import {
		parseTime
	} from '@/utils'
	export default {
		data() {
			return {
				parmas: null, //劵参数
				isShow: false, //显示隐藏控制
				time: parseTime(new Date()),
				prizeratetype: 0 //25 26 代表对应活动类型 25周年 26周年 
			}
		},
		computed: {
			...mapGetters(['userInfo'])
		},
		methods: {
			...mapMutations({
				setCheckCardVolume: 'personal/setCheckCardVolume'
			}),
			...mapActions({
				updateUserMobile: 'login/updateUserMobile',
				getUserInfo: 'login/getUserInfo',
				getCardTopCount: 'personal/getCardTopCount',
				wxlogin: 'login/wxlogin'
			}),
			show(data) {
				let {
					pid,
					pull_qr,
					prizeratetype
				} = data
				this.parmas = {
					pid,
					pull_qr
				}
				this.prizeratetype = prizeratetype
				this.isShow = true
				//中奖了更新顶部信息
				this.getCardTopCount()
			},
			close() {
				this.isShow = false
				this.$emit('closeNotice')
			},
			goCardBag() {
				this.$router.redirectTo({
					url: '/pages/personal/myCardBag/index'
				})
				this.isShow = false
			},
			exchangeBefore(e) {

				this.wxlogin().then(() => {

					this.updateUserMobile(e).then(res => {
						//开始兑换
						this.exchange()
						//异步更新用户信息
						this.getUserInfo(true)

					})

				})

			},
			exchange() {

				this.setCheckCardVolume([this.parmas]) //当前兑换卷
				this.$router.redirectTo({
					url: '/pages/personal/storesCode/index'
				})
				this.isShow = false

			}
		}
	}
</script>

<style lang="scss">
	.win-again {
		.bg-rotate {
			position: absolute;
			width: 720rpx;
			height: 720rpx;
			top: 150rpx;
			z-index: 1;
		}

		.bg-01 {
			position: absolute;
			width: 100%;
			top: 0;
			bottom: 0;
			height: auto;
			z-index: 1;
		}

		.bg-02 {
			position: absolute;
			width: 700rpx;
			height: 710rpx;
			z-index: 1;
			left: 50%;
			top: 50%;
			margin-left: -1250rpx;
			margin-top: -1255rpx;
		}

		.jackpot {
			position: absolute;
			top: 300rpx;
			z-index: 1;
			width: 100%;
		}

		.gift-box-top {
			width: 120rpx;
			height: 79rpx;
			position: absolute;
			z-index: 2;
			left: 50%;
			margin-left: -60rpx;
			top: 300rpx;
		}

		.gift-box-bottom {
			width: 120rpx;
			height: 132rpx;
			position: absolute;
			z-index: 1;
			left: 50%;
			margin-left: -60rpx;
			top: 296rpx;
		}

		.gift-box-again {
			width: 400rpx;
			height: 228rpx;
			position: absolute;
			z-index: 1;
			left: 50%;
			margin-left: -200rpx;
			top: 430rpx;
		}

		// 卡劵
		.card-coupons {
			width: 554rpx;
			height: 148rpx;
			position: absolute;
			top: 415rpx;
			left: 50%;
			margin-left: -269rpx;
			display: flex;
			align-items: center;
			border-radius: 5px;
			overflow: hidden;
			z-index: 3;
			-webkit-animation-delay: 4.5s;
			animation-delay: 4.5s
		}

		.cc-left-icon {
			width: 148rpx;
			height: 148rpx;
		}

		.cc-right-bg {
			position: absolute;
			z-index: -1;
			left: 148rpx;
			height: 148rpx;
			width: auto;
			right: 0;
			top: 0;
		}

		.cc-right-info {
			flex: 1;
			margin-left: 30rpx;
		}

		.cc-r-i-title {
			font-size: 30rpx;
			color: #333;
			font-weight: bold;
			margin-bottom: 15rpx;
		}

		.cc-r-i-time {
			font-size: 22rpx;
			color: #999;
		}

		.xwwh-xh-dialog-btn {
			position: fixed;
			top: 750rpx;
			left: 50%;
			width: 623rpx;
			margin-left: -304rpx;
			padding-top: 20rpx;
			display: flex;
			justify-content: space-around;
			margin-top: 15rpx;
			z-index: 2;
			-webkit-animation-delay: 3.3s;
			animation-delay: 3.3s
		}

		.deposit {
			color: #F5231F;
		}

		.exchange {
			color: #614900;
		}


		.xh-dialog-black {
			background-color: rgba(0, 0, 0, .7);
		}


	}
</style>
